const defaultState = {
    Font: "Тут должен быть шрифт",
    text: "F тут должен быть текст"
};

const fontReducers = (state = defaultState, action) => {
    switch (action.type) {
        case 'CHANGE_FONT':
            return {
                ...state,
                Font: action.payload
            };
        case 'CHANGE_TEXT':
            return {
                ...state,
                text: action.payload
            };
        default:
            return state;
    };
};

export default fontReducers;