import { combineReducers } from 'redux';
import navReducers from '../Store/1Nav/reducers';
import fetchReducers from '../Store/2FetchUser/redusers';
import typeReducers from '../Store/3TypeSelection/reducers';
import materialReducers from '../Store/4MaterialSelection/reducers';
import viewReducers from '../Store/5ViewSelection/reducers';
import shapeReducers from '../Store/6ShapeSelection/reducers';
import sizeReducers from '../Store/7SizeSelection/reducers';
import fontReducers from '../Store/8FontSelection/reducers';
import colorReducers from '../Store/9TextColorSelection/reducers';
import picturesReducers from '../Store/10AddingPictures/reducers';
import backgroundColorReducers from '../Store/11BackgroundColorSelection/reducers';

export default combineReducers({
    nav: navReducers,
    fetch: fetchReducers,
    type: typeReducers,
    material: materialReducers,
    view: viewReducers,
    shape: shapeReducers,
    size: sizeReducers,
    font: fontReducers,
    text: colorReducers,
    pictures: picturesReducers,
    background: backgroundColorReducers,
});