const defaultState = {
    Pictures: 1,
};

const picturesReducers = (state = defaultState, action) => {
    switch (action.type) {
        case "CHANGE_PICTURES":
            return {
                Pictures: action.payload, //Тут должно быть не просто выбор из списка но предложение на загрузить
            };

        default:
            return state;
    }
};

export default picturesReducers;