import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import { ChromePicker } from 'react-color';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

function onChange(obj) {
	console.log(obj);
  };

class BackgroundColorSelection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
		}
		this.go = this.props.go.bind(this);
	}
	render() {
		return (
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ВЫБЕРИТЕ <strong>ЦВЕТ ФОНА</strong>
				</PanelHeader>
				<Group className="Group">
					<Div style={{margin: '20px 20px 20px', textAlign: 'center'}}>
							Выбор цвета
							<div style={{ backgroundColor: '#E27974', padding: 20 }}>
							<ChromePicker onChange={onChange} />
							</div>
					</Div>
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>}
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="addingPictures"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>}
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="sendLayout"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		);
	}
}

BackgroundColorSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		BackgroundColor: state.background.BackgroundColor,
	};
};
  
export default connect(mapStateToProps, actions)(BackgroundColorSelection);