import React from 'react';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import Nastrochnaya from '../img/Nadstrochnaya.png';
import Flag from '../img/Flag.png';
import Loop from '../img/loop.png';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

const ViewSelection = ({go, handleView, Type, View}) => {
		return(
			<Panel id={"viewSelection"} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ВЫБЕРИТЕ <strong>ВИД</strong>
				</PanelHeader>
				<Group className="Group">
					{Type === "Пришивная" &&
						<Div>
							<Div>
								<Button className={View === "Nastrochnaya"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									level="2" 
									onClick={() => handleView("Nastrochnaya")}
								>НАСТРОЧНАЯ</Button>
								<Button className={View === "Flag"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									level="2"
									onClick={() => handleView("Flag")}
								>ФЛАЖОК</Button>
								<Button className={View === "Loop"?
									"logoButton" : "Button"} 
									size="xl"
									stretched
									level="2"
									onClick={() => handleView("Loop")}
								>ПЕТЛЯ</Button>
							</Div>
							<List>
								<Cell multiline>
									{View === "Nastrochnaya" && <img src={Nastrochnaya} alt="Nastrochnaya"/>}
									{View === "Flag" && <img src={Flag} alt="Flag"/>}
									{View === "Loop" && <img src={Loop} alt="loop"/>}
								</Cell>
							</List>
						</Div>
					}
					{Type !== "Пришивная" && <Div style={{backgroundColor: '#ffffff'}} > --- <hr/>... Кроме пришивных бирок никаким другим нельзя присвоить вид. Так же нужно убрать возможность перехода на этот экран если тип не пришивная.
						<hr/>... Перенести весь этот экран в выбор формы бирки.</Div>
					}
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>}
								stretched 
								level="2" 
								onClick={go} 
								data-to="materialSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>}
								stretched 
								level="2" 
								onClick={go} 
								data-to="shapeSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		);
	}

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		Type: state.type.Type,
		View: state.view.View,
	};
};
  
export default connect(mapStateToProps, actions)(ViewSelection);