const defaultState = {
    SizeWidth: 5,
    SizeHight: 10,
}

const sizeReducers = (state = defaultState, action) => {
    switch (action.type) {
        case "HANDLE_WIDTH":
            return {
                ...state,
                SizeWidth: action.payload,
            };
        case "HANDLE_HIGHT":
            return {
                ...state,
                SizeHight: action.payload,
            };
        default:
            return state;
    }
};

export default sizeReducers;