import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, Input, Cell, List, InfoRow, CellButton} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';
//import Adventure from '../fonts/Adventure_2.ttf';
//import Markella_0 from '../fonts/Markella_0.ttf';
//import BetinaScriptC_Bold_2 from '../fonts/BetinaScriptC-Bold_2.otf';

class FontSelection extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			textValue: "",
		}
		this.go = this.props.go.bind(this);
	}
	handleImput = (e) => {
		this.setState({
			textValue: e.target.value
		})
	}

	render() {
		return (
			<Panel id={this.props.id}>
				<PanelHeader>
					ВЫБЕРИТЕ <strong>ШРИФТ</strong>
				</PanelHeader>
					<Group className="Group">
						<List>
							<Input
								top="Имя" 
								placeholder="Введите текст"
								alignment="center"
								onChange={this.handleImput}
							/>
						</List>	
						<CellButton 
							style={{fontFamily: "Adventure Regular",
									backgroundColor: "white"}}
							onClick={this.go}
							data-to="textColorSelection">
							1 {this.state.textValue}
						</CellButton>
						<InfoRow>
							<Cell multiline>
								<div style={{fontFamily: "Adventure Regular"}} >
									{this.state.textValue}
								</div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Raleway Light"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Rosa Marena Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<div style={{fontFamily: "Segoe Print"}} >
									{this.state.textValue}
								</div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Still Time Cyr Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "StudioScriptC Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<div style={{fontFamily: "AllegroScript"}} >
									{this.state.textValue}
								</div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Annabelle Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Aquarelle Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "ArtScript Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<div style={{fontFamily: "Aurora Script Regular"}} >
									{this.state.textValue}
								</div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Bello Pro"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "BetinaScriptC"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Cansellarist Cansellarist"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Carolina Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Hitch Hike"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "JakobC Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Lemon Tuesday"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Markella Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Myriad Pro"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Myriad Pro Bold"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<InfoRow>
							<Cell multiline>
								<Div style={{fontFamily: "Teddy Bear Regular"}} >
									{this.state.textValue}
								</Div>
        	        		</Cell>
						</InfoRow>
						<FixedLayout vertical="bottom">
							<Div style={{display: 'flex'}}>
								<Button 
									className="Button" 
									size="xl"
									after={<img src={onLeft} alt="onLeft"/>}
									stretched 
									level="2" 
									onClick={this.go} 
									data-to="shapeSelection"
								></Button>
								<Button 
									className="Button" 
									size="xl"
									after={<img src={onRight} alt="onRight"/>}
									stretched 
									level="2" 
									onClick={this.go} 
									data-to="textColorSelection"
								></Button>
							</Div>
						</FixedLayout>
					</Group>
			</Panel>
		);
	}
}

FontSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		Font: state.font.font,
		text: state.font.text,
	};
};
  
export default connect(mapStateToProps, actions)(FontSelection);