import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';
import bumYarlik from '../img/bumYarlik.svg';
import nakleyka from '../img/nakleyka@4x.svg';
import prishivnaya from '../img/prishivnaya.svg';
import termo from '../img/termo@4x.svg';

class TypeSelection extends React.Component {
	constructor(props) {
		super(props);

		this.go = this.props.go.bind(this);
		this.handleType = this.props.handleType.bind(this);	
	}

	render() {
		return(
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ВЫБЕРИТЕ <strong>ТИП</strong>
				</PanelHeader>
				<Group className="Group1">
					<Div>
			            <Button className={this.props.Type === "Пришивная"?
			                    "logoButton" : "Button"}  
			                after={<img src={prishivnaya} alt="prishivnaya"/>}
			                size="xl"
			                stretched level="2"
							onClick={() => this.handleType("Пришивная")}
			            >ПРИШИВНАЯ</Button>
			            <Button className={this.props.Type === "Бумажный ярлык"?
			                    "logoButton" : "Button"}  
			                after={<img src={bumYarlik} alt="bumYarlik"/>}
			                size="xl"
			                stretched level="2"
			                onClick={() => this.handleType("Бумажный ярлык")}
			            >БУМАЖНЫЙ ЯРЛЫК</Button>
			            <Button className={this.props.Type === "Термонаклейка"?
			                    "logoButton" : "Button"}  
			                after={<img src={termo} alt="termo"/>}
			                size="xl"
			                stretched level="2"
			                onClick={() => this.handleType("Термонаклейка")}
			            >ТЕРМО</Button>
			            <Button className={this.props.Type === "Наклейка"?
			                    "logoButton" : "Button"}  
			                after={<img src={nakleyka} alt="nakleyka"/>}
			                size="xl"
			                stretched level="2"
			                onClick={() => this.handleType("Наклейка")}
			            >НАКЛЕЙКА</Button>
			            <List>
			                <Cell multiline>
			                    {this.props.Type === "Пришивная" && <Div style={{backgroundColor: '#ffffff'}} >Пришивная <hr/> Сатин, репс, силикон, хлопок, кожкартон.</Div>}
			                    {this.props.Type === "Бумажный ярлык" && <Div style={{backgroundColor: '#ffffff'}} >Бумажный ярлык <hr/> Белый или крафт-картон плотностью 250гр</Div>}
			                    {this.props.Type === "Термонаклейка" && <Div style={{backgroundColor: '#ffffff'}} >Термонаклейка <hr/> Термонаклейка на белом фоне, приклеивается утюгом, стойкая печать, несколько цветов на выбор</Div>}
			                    {this.props.Type === "Наклейка" && <Div style={{backgroundColor: '#ffffff'}} >Наклейка <hr/> Бумажная наклейка с полноцветной печатью, любой формы. Максимальный размер 9см</Div>}
			                </Cell>
			            </List>
			        </Div>
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>}
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="welcomeScreen"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="materialSelection"
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>	
		);
	}
}

TypeSelection.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		Type: state.type.Type,
	};
};
  
export default connect(mapStateToProps, actions)(TypeSelection);