import React from 'react';
import PropTypes from 'prop-types';
import logo from '../img/logo0.png';
import '../style.css';
import { Panel, Button, Group, Div, PanelHeader} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';

const Home = ({ go }) => {
		return(
			<Panel id={"home"} style={{overflow: 'hidden'}}>
				<PanelHeader>
					"Tag Constructor"
				</PanelHeader>
				<Group className="Group">
					<Div>
						<img className="logo" src={logo} alt="Birki Firma Logo"/>
						<p className="p">Добро пожаловать!!!</p>
					</Div>
					<Div style={{display: 'flex'}}>
							<Button 
							className="logoButton"
							size="xl" 
							level="2" 
							onClick={go} 
							data-to="welcomeScreen"
						>Создай свою бирку!!!</Button>
						</Div>	
				</Group>
			</Panel>
		);
	}
//}
Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
	};
};
  
export default connect(mapStateToProps, actions)(Home);
