import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

class SendLayout extends React.Component {
	constructor(props) {
		super(props);
		this.state = {

		}
		this.go = this.props.go.bind(this);
	}
	render() {
		return (
			<Panel id={this.props.id} style={{overflow: 'hidden'}}>
				<PanelHeader>
					ОТПРАВКА ЗАКАЗА
				</PanelHeader>
				<Group className="Group">
					Вот сюда надо финальный скрипт воткнуть.
			
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched 
								level="2" 
								onClick={this.go} 
								data-to="backgroundColorSelection"
							></Button>
							<Button 
								className="Button" 
								size="xl"
								after={<img src={onRight} alt="onRight"/>} 
								stretched 
								level="2" 
								onClick={console.log(this.props)}
							></Button>
						</Div>
					</FixedLayout>
				</Group>
			</Panel>
		);
	}
}

SendLayout.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		fetchedUser: state.fetch.fetchedUser,
		Type: state.type.Type,
		Material: state.material.Material,
		View: state.view.View,
		Shape: state.shape.Shape,
		SizeWidth: state.size.SizeWidth,
		SizeHight: state.size.SizeHight,
		Font: state.font.font,
		text: state.font.text,
		TextColor: state.text.TextColor,
		Pictures: state.pictures.Pictures,
		BackgroundColor: state.background.BackgroundColor,
	};
};
  
export default connect(mapStateToProps, actions)(SendLayout);