const WelcomeScreen = ({ go }) => {
		return(
			<Panel id={"welcomeScreen"} style={{overflow: 'hidden'}}>
				<PanelHeader>
					О КОНСТРУКТОРЕ
				</PanelHeader>
				<Group className="Group">
					<Div style={{ textAlign: "center"}}>Здравствуйте. 

					<p>Мы предлагаем Вам самостоятельно создать макет бирки для печати. Если у вас возникли вопросы, то
    		        напишите нам, и мы с радостью ответим.</p></Div>
					<FixedLayout vertical="bottom">
						<Div style={{display: 'flex'}}>
							<Button 
								className="Button"
								size="xl" 
								after={<img src={onLeft} alt="onLeft"/>} 
								stretched level="2" 
								onClick={go} 
								data-to="home"
								>
							</Button>
							<Button 
								className="Button"
								size="xl" 
								after={<img src={onRight} alt="onRight"/>} 
								stretched level="2" 
								onClick={go} 
								data-to="typeSelection"
								>
							</Button>
						</Div>	
    		  	    </FixedLayout>
				</Group>
			</Panel>
		);
	}
//}

WelcomeScreen.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
	};
};
  
export default connect(mapStateToProps, actions)(WelcomeScreen);
