import React from 'react';
import {Panel, PanelHeader, Group, Button, Div, FixedLayout, List, Cell} from '@vkontakte/vkui';
import { connect } from 'react-redux';
import * as actions from '../Store/actions';
import '../style.css';
import onRight from '../img/onRight.png';
import onLeft from '../img/onLeft.png';

const MaterialSelection = ({Material, Type, go, handleMaterial}) => {
	return(
		<Panel id={"materialSelection"} style={{overflow: 'hidden'}}>
			<PanelHeader>
				ВЫБЕРИТЕ <strong>МАТЕРИАЛ</strong>
			</PanelHeader>
			{Type === "Пришивная" && 
				<Group className="Group">
					<Div>
       	    		    <Button className={Material === "Сатин"?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("Сатин")}
       	    		    >Сатин</Button>
						<Button className={Material === "Репс"?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("Репс")}
       	    		    >Репс</Button>
						<Button className={Material === "Силикон"?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("Силикон")}
       	    		    >Силикон</Button>
						<Button className={Material === "Хлопок"?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("Хлопок")}
       	    		    >Хлопок</Button>
						<Button className={Material === "Кожкартон"?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("Кожкартон")}
       	    		    >Кожкартон</Button>
       	    		    <Button className={Material === "ПолиХлопок"?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
       	    		        level="2"
       	    		        onClick={() => handleMaterial("ПолиХлопок")}
       	    		    >ПолиХлопок</Button>
					</Div>
      	    	    <List>
       	    	        <Cell multiline>
       	    	            {Material === "Сатин" && <Div style={{backgroundColor: '#ffffff'}} >Сатин <hr/>Тонкий, мягкий, с лёгким блеском. Отлично передаёт четкие детали изображения, печать очень стойкая.</Div>}
       	    	            {Material === "Репс" && <Div style={{backgroundColor: '#ffffff'}} >Репс <hr/>Плотный, рубчатый, фактурный материал, печать очень стойкая.</Div>}
       	    	            {Material === "Силикон" && <Div style={{backgroundColor: '#ffffff'}} >Силикон <hr/>Полупрозрачный, матовый и очень надежный материал, печать может быть только черного, синего, красного, зеленого, розового, золотого и серебряного цвета.</Div>}
       	    	            {Material === "Хлопок" && <Div style={{backgroundColor: '#ffffff'}} >Хлопок <hr/>100% натуральный Хлопок полотняного плетения, печать выдерживает до 20 стирок.</Div>}
       	    	            {Material === "Кожкартон" && <Div style={{backgroundColor: '#ffffff'}} >Кожкартон <hr/> Печать лучше выполнять тёмными цветами, например, чёрным, красным или синим.</Div>}
       	    	            {Material === "ПолиХлопок" && <Div style={{backgroundColor: '#ffffff'}} >ПолиХлопок <hr/> Имитация хлопка. Рубчатый, плотный, фактурный материал. 100% ПЭ. Устойчив к стирке.</Div>}
       	    	        </Cell>
       	    	    </List>
				</Group>
			}
			{Type === "Бумажный ярлык" && 
				<Group className="Group">
					<Div>
       	    		    <Button className={Material === "Белый"?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("Белый")}
       	    		    >Белый</Button>
						<Button className={Material === "Крафт-картон"?
       	    		        "logoButton" : "Button"} 
       	    		        stretched
       	    		        size="xl"
       	    		        level="2"
       	    		        onClick={() => handleMaterial("Крафт-картон")}
       	    		    >Крафт-картон</Button>
					</Div>
      	    	    <List>
       	    	        <Cell multiline>
       	    	            {Material === "Белый" && <Div style={{backgroundColor: '#ffffff'}} >Белый <hr/>...</Div>}
       	    	            {Material === "Крафт-картон" && <Div style={{backgroundColor: '#ffffff'}} >Крафт-картон <hr/>...</Div>}
       	    	        </Cell>
       	    	    </List>
				</Group>
			}
			{Type === "Термонаклейка" && 
				<Group className="Group">
					<Div>
						Тут отдельный материал. его проще выбирать по умолчанию.
					</Div>
      	    	    <List>
       	    	        <Cell multiline>
       	    	            <Div style={{backgroundColor: '#ffffff'}} >Термонаклейка <hr/>...</Div>
       	    	        </Cell>
       	    	    </List>
				</Group>
			}
			{Type === "Наклейка" && 
				<Group className="Group">
					<Div>
       	    		    <Button className={Material === "Глянец"?
       	    		        "logoButton" : "Button"} 
       	    		        size="xl"
       	    		        stretched
							level="2" 
       	    		        onClick={() => handleMaterial("Глянец")}
       	    		    >Глянец</Button>
						<Button className={Material === "Матовый"?
       	    		        "logoButton" : "Button"} 
       	    		        stretched
       	    		        size="xl"
       	    		        level="2"
       	    		        onClick={() => handleMaterial("Матовый")}
       	    		    >Матовый</Button>
					</Div>
      	    	    <List>
       	    	        <Cell multiline>
       	    	            {Material === "Глянец" && <Div style={{backgroundColor: '#ffffff'}} >Глянец <hr/>...</Div>}
       	    	            {Material === "Матовый" && <Div style={{backgroundColor: '#ffffff'}} >Матовый <hr/>...</Div>}
       	    	        </Cell>
       	    	    </List>
				</Group>
			}
			<FixedLayout vertical="bottom">
				<Div style={{display: 'flex'}}>
					<Button 
						className="Button" 
						size="xl"
						after={<img src={onLeft} alt="onLeft"/>} 
						stretched 
						level="2" 
						onClick={go} 
						data-to="typeSelection"
					></Button>
					<Button 
						className="Button" 
						size="xl"
						after={<img src={onRight} alt="onRight"/>}
						stretched 
						level="2" 
						onClick={go} 
						data-to="viewSelection"
					></Button>
				</Div>
			</FixedLayout>
		</Panel>
	);
}	

const mapStateToProps = (state) => {
	return {
		activePanel: state.nav.activePanel,
		Type: state.type.Type,
		Material: state.material.Material,
	};
};
  
export default connect(mapStateToProps, actions)(MaterialSelection);